import React from 'react'
import SEO from './seo'
import { CenterContainer, NameContainer, NameWrapper, FirstNameWrapper, LastNameWrapper,Bio } from "./styles/HeroElements"

const Hero = () => {
    return (
        <> 
            <SEO title="Home" />
            <CenterContainer id="home">
                <NameContainer>
                    <div>
                        <NameWrapper>
                            <FirstNameWrapper>
                            <p>ketan</p>
                            </FirstNameWrapper>
                            <LastNameWrapper>
                            <p>sontakke</p>
                            </LastNameWrapper>
                        </NameWrapper>
                        <Bio id="bio">computer engineer • motion designer</Bio>
                    </div>
                    
                </NameContainer>

            </CenterContainer>
        </>
        
            
        
        
    )
}

export default Hero
