import React, {useState} from 'react'
import {Container,HeadingContainer,ContentContainer,ContentWrapper,LeftArrow,RightArrow,Content,LeftContent,RightContent,TimelineContainer, TimelineWrapper, Desc, Tech, H1, Span} from './styles/PortfolioElements'
import HorizontalTimeline from 'react-horizontal-timeline'
import {SiAdobepremiere, SiAdobeaftereffects, SiReact, SiFirebase, SiHtml5, SiCss3, SiJquery, SiBootstrap} from 'react-icons/si'
import ImageSlider from './ImageSlider'
import {BabyVaccine} from './SliderData'

const Portfolio = () => {
    const timelineStyles = { background: 'transparent', foreground: '#ebe0a6', outline: '#dfdfdf' };
    const timelineValues = [new Date() ,'MAR 17 2021' , 'MAR 11 2019' ,'AUG 8 2016', 'JUN 9 2016', 'DEC 20 2015', 'MAR 17 2014'];
    const [tlState, setTlState] = useState({value:0, previous:0});
    const content ={
        0:{
            title: 'ShakeOnIt',
            desc: 'A website built using React and Firebase. A work in progress.',
            content: <img src="https://i.gyazo.com/2ccf6f4e6897538ed85eae04b3846770.gif" alt="ShakeOnIt Gif" width="475px" height="275px" />,
            tech: {react:true, fire:true}
        },
        1:{
            title: 'BellaCasa',
            desc: 'A short animated intro for BellaCasa using Adobe After Effects.',
            content: <iframe title="BellaCasa" width="475px" height="275px" src="https://www.youtube.com/embed/NK3iLPR2xZw" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>,
            tech: {pr:false, ae:true}
        },
        2:{
            title: 'Baby Vaccination Tracker',
            desc: 'A simple HTML & CSS Website to track your baby\'s vaccinations.',
            content: <ImageSlider slides={BabyVaccine}/>,
            tech: {html:true, css:true, bootstrap:true, jq:true}
        },
        3:{
            title: 'Frank & Oak Ottawa Promotional Video',
            desc: 'A promotional video created to explore Frank & Oak\'s 2016 fall collection in Ottawa\'s lively Westboro village.',
            content: <iframe title="Frank and Oak Ottawa Promotional Video" src="https://player.vimeo.com/video/178110788" width="475px" height="275px" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen></iframe>,
            tech: {pr:true, ae:true}
        },
        4:{
            title: '2016 Enactus Nationals Finalist Presentation Outro',
            desc: 'An outro video created for the 2016 Enactus Canadian national finalist presentations.',
            content: <iframe title="2016 Enactus Nationals Finalist Presentation Outro" width="475px" height="275px" src="https://www.youtube.com/embed/9E0zJQyqFIU" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>,
            tech: {pr:true, ae:true}
        },  
        5:{
            title: 'Enactus uOttawa Digital Media Submission',
            desc: 'A short animated intro for Enactus uOttawa that landed me a Digital Marketing position on the team.',
            content: <iframe title="Enactus uOttawa Digital Media Submission" width="475px" height="275px" src="https://www.youtube.com/embed/MXt3UEZjeWQ" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>,
            tech: {pr:false, ae:true}
        },   
        6:{
            title: 'Euphonic7 Music Pack Promo',
            desc: 'A quick animation to promote the release of Euphonic7\'s music pack.',
            content:<iframe title="Euphonic7 Music Pack Promo" src="https://player.vimeo.com/video/89353951" width="475px" height="275px" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen></iframe>,
            tech: {pr:false, ae:true}
        },

    }

    return (
        <>
            <Container id="portfolio">
                <HeadingContainer>
                    <h1>portfolio</h1>
                </HeadingContainer>
                <ContentContainer>
                    <ContentWrapper>
                        <LeftContent>
                            {/* <LeftArrow disabled={true}/> */}
                                <Content>{content[tlState.value].content}</Content>
                            {/* <RightArrow /> */}
                        </LeftContent>
                        <RightContent>
                            <div>
                            <H1> {content[tlState.value].title}</H1>
                            <Desc> {content[tlState.value].desc}</Desc>
                            <Tech>
                                <Span show={content[tlState.value].tech.ae}>{  <SiAdobeaftereffects/>}</Span>
                                <Span show={content[tlState.value].tech.pr}>{  <SiAdobepremiere/>}</Span>
                                <Span show={content[tlState.value].tech.react}>{  <SiReact/>}</Span>
                                <Span show={content[tlState.value].tech.fire}>{<SiFirebase/>}</Span>
                                <Span show={content[tlState.value].tech.html}>{<SiHtml5/>}</Span>
                                <Span show={content[tlState.value].tech.css}>{<SiCss3/>}</Span>
                                <Span show={content[tlState.value].tech.bootstrap}>{<SiBootstrap/>}</Span>
                                <Span show={content[tlState.value].tech.jq}>{<SiJquery/>}</Span>
                            </Tech>
                            </div>
                            
                        </RightContent>
                    </ContentWrapper>
                    <TimelineContainer>
                        <TimelineWrapper >
                            <HorizontalTimeline 
                            index={tlState.value}
                            indexClick={(index) => {
                                setTlState({ value: index, previous: tlState.value });
                            }}
                            values={timelineValues}
                            labelWidth={100}
                            styles = {timelineStyles}
                            
                            
                            />
                        
                        </TimelineWrapper>
    {/*                     
                        <div>
                            this is timeline item, some sort of mapping here
                        </div> */}
                    </TimelineContainer>
                </ContentContainer>  
            </Container>
        </>
        
    )
}

export default Portfolio
