import React from 'react'
import {Container, HeadingContainer,ContentContainer, ContentWrapper, LeftContent,RightContent, Connect, Other} from './styles/AboutElements'
import {FaLinkedin, FaGithubSquare} from 'react-icons/fa'
import {Link} from 'gatsby'

const About = () => {
    return (

        <>
            <Container id="about">
                <HeadingContainer>
                    <h1>about me</h1>
                </HeadingContainer>
                <ContentContainer>
                    <ContentWrapper>
                            <LeftContent>
                                <div>
                                    <h2>I am a 2019 Computer Engineering graduate from the University of Ottawa.</h2>
                                    <p>Thank you for taking the time to check out my website. I built this website to showcase some of the works from my web development, editing, and motion design hobbies. I've been 
                                        creating videos and animated intros using Adobe After Effects and Cinema 4D for clients since 2011 as a side hobby. 
                                        I've recently started to learn the latest web technologies using Gatsby, React, and NodeJS. You can find some of my web work above (I also built this site using Gatsby). I am constantly on the lookout for new techniques and ideas so that I can apply them in future projects.
                                        I also have a more technical background, which you can find more information about in my more simple website on the right.
                                    </p>
                                </div>
                                
                            </LeftContent>
                            <RightContent>
                                <div>
                                    <Connect>
                                        <h2>Connect with me!</h2>
                                        <span><Link to='https://www.linkedin.com/in/ketan-sontakke-394145127/' target="_blank" rel="noopener noreferrer"><FaLinkedin /></Link></span>
                                        <span><Link to='https://github.com/ketan8O8' target="_blank" rel="noopener noreferrer"><FaGithubSquare /></Link></span>
                                    </Connect>
                                    <Other>
                                        <h2>Checkout my other <Link to='https://ketan8o8.github.io/' target="_blank" rel="noopener noreferrer">website</Link> with more technical info about me.</h2>
                                    </Other>
                                </div>
                                
                                
                            </RightContent>
                    </ContentWrapper>
                </ContentContainer>
                
            </Container>
        </>
        
    )
}

export default About
