import React, {useState} from 'react'
import {BabyVaccine} from './SliderData'
import {RiArrowDropLeftLine, RiArrowDropRightLine} from 'react-icons/ri'
import './styles/slider.css'

const ImageSlider = ({slides}) => {
    const [current, setCurrent] = useState(0);
    const length = slides.length;

    const nextSlide = () =>{
        setCurrent(current ===length -1 ? 0 : current + 1);
    };
    const prevSlide = () =>{
        setCurrent(current === 0 ? length - 1 : current - 1);
    };

    if (!Array.isArray(slides) || slides.length <=0){
        return null;
    }
    return (
        <section className="slider">
            <RiArrowDropLeftLine className="left-arrow" 
            onClick={prevSlide}
            />
            
           {BabyVaccine.map((slide,index) => {
               return (
                   <div className={index === current ? 'slide active' : 'slide'} key={index}>
                       {index === current && <img src={slide.gif} alt="Baby Vaccine Gifs" width="475px" height="275px"  className='image'/>}
                   </div>
               )
           })}
           <RiArrowDropRightLine className="right-arrow" 
           onClick={nextSlide}
           />
        </section>
    );
};

export default ImageSlider;
