import styled from 'styled-components'

export const Container = styled.div`
    display:flex;
    flex-direction: column;
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right:50px;
    padding-left:50px;
    height:100vh;
    
    /* align-items:center; */
    justify-content:center;
    scroll-snap-align:center;
    @media screen and(max-width: 991px){
        
        padding-right:30px;
        padding-left:30px;
    }    
`;

export const HeadingContainer = styled.div`
    display:flex;
    flex-wrap: column wrap;
    margin-top: 50px;
    > h1 {
        color: white;
        font-family: 'Michroma', sans-serif; 
        font-size: 64px;
        /* -webkit-text-stroke: 1px white;
        -webkit-text-fill-color: transparent; */
    }
    
`;

export const ContentContainer = styled.div`
    margin-top: 60px;
    display:flex;
    flex-flow: column wrap;
    
    text-align:justify;
`;

export const ContentWrapper = styled.div`
    display:flex;
    flex-flow: row wrap;

`;

export const LeftContent = styled.div`
    flex: 0 0 50%;
    padding-right:20px;

    div > h2 {
        color:white;
        font-weight: bold;
    }
    div > p {
        color:white;
        margin-top: 10px;
    }

    @media screen and (max-width: 768px){
        max-width:100%;
        flex-basis: 100%;
        display: flex;
        justify-content:center;
    }
    
`;
export const RightContent = styled.div`
    flex: 0 0 50%;
    padding-left:20px;
    color:white;
    
    @media screen and (max-width: 768px){
        max-width:100%;
        flex-basis: 100%;
        display: flex;
        padding-left:unset;
        margin-top: 10px;
    }
`;
export const Connect = styled.div`
    > h2 {
        color:white;
        font-weight: bold;
    }
    > span, a {
        font-size: 2.5rem;
        text-decoration:none;
        color:white;
        margin-right: 2px;
        :hover{
            cursor: pointer;
        }
    }

    
`;
export const Other = styled.div`
    > h2 {
        color:white;
        font-weight: bold;
    }

    h2 > a{
        text-decoration:none;
        color: #ebe0a6;
        font-weight: bold;
    }
`;